import axios from 'axios';
import router from '@/router';

export default{
    state:{       
       bikedata:[],
       areanameArray:[],
       areaname:'',
       cityname:'',
       areanameDataArray:[],
       centrePoint:[],
    },
    getters:{   
          getAreanameArray(state){
              return state.areanameArray;
          },
          getAreaName(state){
              return state.areaname;
          },
          getCityName(state){
            return state.cityname;
          },
          getCentrePoint(state){
            return state.centrePoint;
          },
          gerareanameDataArray(state){
            return state.areanameDataArray;
          },
    },
    actions:{
        FilterUbikeData(context,status){   
            var obj=[];  
            var api='';   
            if(status=='台北') 
            { api = 'https://tcgbusfs.blob.core.windows.net/blobyoubike/YouBikeTP.json';}   
            if(status=='高雄') 
            { api='https://api.kcg.gov.tw/api/service/Get/b4dd9c40-9027-4125-8666-06bef1756092';}

            fetch(		 
                 api
            )
              .then((res) => {			 
                return res.json();  
              })
              .then((res) => {
                if(status=='台北'){
                  obj=Object.keys(res).map((key) => [Number(key), res[key]]);                
                  context.commit('bikefilter',obj[1]);
                }	  
                if(status=='高雄'){
                  obj=res.data.retVal;                
                  context.commit('bikefilterToKSH',obj);
                }	                                  
                
              } )
              .catch(() => {			 
                alert("取得資料api發生錯誤!");
              });  
        },  
        FilterUbikeDataSelected(context,status){   
          if(status=='台北'){ context.commit('bikefilterdata');}
          if(status=='高雄'){ context.commit('bikefilterdataToKSH');}           
      },  
        uploadSelectedAreaNameAction(context,status){
          context.commit('uploadSelectedAreaName',status);
        },
        uploadSelectedCityNameAction(context,status){
          context.commit('updateSelectCityName',status);
        },
    },
    mutations:{      
        bikefilter(state,status){           
            state.bikedata=status; 
            state.areanameArray=[];
            //console.log(status);
               for (const item of Object.entries(status[1])) {                
                for (const [key] of Object.entries(item)) {                    
                  for(const[key1,value1] of Object.entries(item[key])){                 
                     if(key1=='sarea'){ if(state.areanameArray.some(x => x == value1)==false){state.areanameArray.push(value1);}}                                  
                  }// end (item[key])
                } // end (item)
              } //end (status[1])  
        },
        bikefilterToKSH(state,status){           
          state.bikedata=status; 
          state.areanameArray=[];
             for (const item of status) {  
              if(state.areanameArray.some(x => x == item.sarea)==false){state.areanameArray.push(item.sarea);}
            } //end (status)  
      },
        bikefilterdata(state,status){
          //console.log(state.areaname);
          var id=0;
          var datalist=state.bikedata; 
          state.areanameDataArray=[];
          for (const item of Object.entries(datalist[1])) {                
            for (const [key] of Object.entries(item)) {      
              var issame=false; 
              var areaname='';
              var lat='';//1
              var lng='';//2
              var bemp='';//可停空位剩餘
              var sbi='';//可租借車輛剩餘
              var sna='cccc';//中文地名名稱
              var mday='';//更新日期              
              for(const[key1,value1] of Object.entries(item[key])){   
               
                 if(key1=='sarea'){ areaname=value1; issame=value1==state.areaname?true:false;}
                 if(key1=='lat'){lat=value1;}
                 if(key1=='lng'){lng=value1;}
                 if(key1=='bemp'){bemp=value1;}
                 if(key1=='sbi'){sbi=value1;}
                 if(key1=='sna'){sna=value1;}
                 if(key1=='mday'){mday=value1;}
              }// end (item[key])
              if(issame==true)
              {
                   id=id+1;
                   if(id==1){state.centrePoint=[lat,lng];}   
                   state.areanameDataArray.push({
                   id:id,
                   name:sna,
                   local:[lat,lng],
                   stock:bemp,
                   remaining:sbi,
                   upddate:mday,
                });              
              }
            } // end (item)
          } //end (status[1])           
        },
        bikefilterdataToKSH(state,status){         
          var id=0;
          var datalistKSH=state.bikedata; 
          state.areanameDataArray=[];
          for (const item of datalistKSH) {                
              if(item.sarea==state.areaname)
              {     id=id+1;   
                    if(id==1){
                      state.centrePoint=[];state.centrePoint=[item.lat,item.lng];
                      console.log('test begin');
                      console.log(item.lat,item.lng);
                      console.log('test end');
                    }    
                    state.areanameDataArray.push({
                    id:id,
                    name:item.sna,
                    local:[item.lat,item.lng],
                    stock:item.bemp,
                    remaining:item.sbi,
                    upddate:item.mday,
                 });  
              }
          } //end (datalistKSH)           
        },// END bikefilterdataToKSH
        uploadSelectedAreaName(state,status){
          state.areaname=status;
        },
        updateSelectCityName(state,status)
        {
              state.cityname=status;
        },
    }
}


