<template>   
   <div>
	    <banner></banner>

		<serviceArea></serviceArea>	
			
        <aboutArea></aboutArea>
			
		<qualificationArea></qualificationArea>
			
		<projectArea></projectArea>
		
        <featureArea></featureArea>

		<contactArea></contactArea>

		<footerArea></footerArea>			
   </div>
</template>

<script> 
//Resume Detail Components  
//Create by davidlee 20200724
import banner from '@/components/resume/detail/banner';
import serviceArea from '@/components/resume/detail/serviceArea';
import aboutArea from '@/components/resume/detail/aboutArea';
import qualificationArea from '@/components/resume/detail/qualificationArea';
import projectArea from  '@/components/resume/detail/projectArea';
import featureArea from '@/components/resume/detail/featureArea';
import contactArea from '@/components/resume/detail/contactArea';
import footerArea from '@/components/resume/detail/footerArea';

//Vuex Funciton
import { mapActions } from 'vuex';
import { mapGetters } from 'vuex'; 
export default {
     name: 'LoginNew',
  data () {
    return {
     user:{
        username:'banni442@gmail.com',
        password:'teco1212',      
         },
           }
  },
  components:{
		 banner,
		 serviceArea,
		 aboutArea,
		 qualificationArea,
		 projectArea,
		 featureArea,
		 contactArea,
		 footerArea,
  },
  methods:{        
         ...mapActions({           
           signin:'signin',
         }),  
  },
  computed:{
      ...mapGetters(
        {
          msg:'getmsg',
          logintitle:'getLogintitle',
        }),
  },
}
</script>


<style lang="scss" scoped>
.p1-gradient-bg, .primary-btn, .primary-btn2:hover, .default-header, .dropdown-menu, .banner-area, .myself-wrap .desc .talk-btn, .skillbar-bar, .single-feature:hover, .single-footer-widget .click-btn, .generic-banner {
  background-image: -moz-linear-gradient(0deg, #62bdfc 0%, #8490ff 100%);
  background-image: -webkit-linear-gradient(0deg, #62bdfc 0%, #8490ff 100%);  
}

</style>


