<template>
        <nav class="col-md-2 d-none d-md-block bg-light sidebar">
          <div class="sidebar-sticky">
            
            <ul class="nav flex-column">
              <li class="nav-item">
                <a class="nav-link active" href="#">
                  <span data-feather="home"></span>
                  <h6>縣市</h6> <span class="sr-only">(current)</span>
                </a>
              </li>
              <li class="nav-item">
                <a class="nav-link" >
                  <div class="dropdown">
                     <a class="btn btn-secondary dropdown-toggle" href="#" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                          {{CityName}}
                     </a>
                     <div class="dropdown-menu" aria-labelledby="dropdownMenuLink">
                             <a class="dropdown-item" href="#" v-for="item in cityData" :key="item" @click="selectCity(item)">
                             {{item}}
                          </a>    
                     </div>
                  </div>
                </a>
              </li>            
            </ul>

<br>
<br>

           <ul class="nav flex-column">
              <li class="nav-item">
                <a class="nav-link active" href="#">
                  <span data-feather="home"></span>
                  <h6>區域</h6> <span class="sr-only">(current)</span>
                </a>
              </li>
              <li class="nav-item">
                <a class="nav-link" >
                  <div class="dropdown">
                     <a class="btn btn-secondary dropdown-toggle" href="#" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        {{AreaName}}
                    </a>
                       <div class="dropdown-menu" aria-labelledby="dropdownMenuLink">
                          <a class="dropdown-item" href="#" v-for="item in AreanameArray" :key="item" @click="selectArea(item)">
                             {{item}}
                          </a>                        
                       </div>
                 </div>
                </a>
              </li>            
            </ul>

          </div>
    
        </nav>
</template>
<script>
import { mapActions } from 'vuex';
import { mapGetters } from 'vuex';
export default {
  data() {
    return {
      //selectvalue:'',
      cityData:['台北','高雄'],
    }
  },
  methods: {
      ...mapActions({           
           uploadSelecedValue:'uploadSelectedAreaNameAction',
           uploadSelectCityValue:'uploadSelectedCityNameAction',
           filterSelectedVaue:'FilterUbikeDataSelected',
           FilterUbikeData:'FilterUbikeData',
		 }), 
    selectArea(value){
      this.uploadSelecedValue(value);     
      this.filterSelectedVaue(this.CityName);
    },
    selectCity(value){
       this.uploadSelectCityValue(value);   
       this.filterSelectedVaue(this.CityName); 
       if(value=='台北')
       {       
          this.uploadSelecedValue('信義區'); 
       }
       if(value=='高雄')
       {           
          this.uploadSelecedValue('鹽埕區');          
       }
       this.FilterUbikeData(this.CityName);  
    },
  },
  computed: {
    ...mapGetters(
        {
          AreanameArray:'getAreanameArray', 
          AreaName:'getAreaName',  
          CityName:'getCityName',
          SelectData:'gerareanameDataArray',       
        }),
  },
  mounted() {
    this.uploadSelecedValue('信義區');
    this.uploadSelectCityValue('台北');
  },
  beforeUpdate() {
     this.filterSelectedVaue(this.CityName);    
  },
}
</script>
<style lang="scss" scoped>
#dropdownMenuLink{
    width: 200px !important;   
}
.dropdown-menu {
    width: 200px !important;    
}
</style>


