// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Vuex from 'vuex'
//import BootstrapVue from 'bootstrap-vue'
//import 'bootstrap/dist/css/bootstrap.css'
//import 'bootstrap-vue/dist/bootstrap-vue.css'
import axios from 'axios'
import qs from 'qs'
import VueAxios from 'vue-axios'
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import VueFeather from 'vue-feather';
import 'bootstrap'

import App from './App'
import router from './router'
import store from './store'
import bus from './bus'
import currencyFilter from './filters/currency'
import globalFunc from '@/libs/globalFunc'
import {ServerTable, ClientTable, Event} from 'vue-tables-2';


import { LMap, LTileLayer, LMarker, LPopup, LIcon } from "vue2-leaflet";//npm install vue2-leaflet leaflet --save
// 載入 css
import "leaflet/dist/leaflet.css";




//Vue.use(BootstrapVue)
Vue.use(Vuex);
Vue.config.productionTip = false
Vue.use(VueAxios, axios)
//Vue.use(qs)
Vue.component('feather', VueFeather);
Vue.component('Loading', Loading);
Vue.filter('currency',currencyFilter); //將filter的currency.js，註冊為全域。Create by DavidLee 20200714
Vue.use(globalFunc)
Vue.use(ClientTable); //Client table
Vue.use(ServerTable); //Server table

// 啟用載入的各組件
Vue.component("l-map", LMap);
Vue.component("l-tile-layer", LTileLayer);
Vue.component("l-marker", LMarker);
Vue.component("l-popup", LPopup);
Vue.component("l-icon", LIcon);
import { Icon } from "leaflet";
delete Icon.Default.prototype._getIconUrl;
Icon.Default.mergeOptions({
  iconRetinaUrl: require("leaflet/dist/images/marker-icon-2x.png"),
  iconUrl: require("leaflet/dist/images/marker-icon.png"),
  shadowUrl: require("leaflet/dist/images/marker-shadow.png")
});


axios.defaults.withCredentials=true;
axios.defaults.headers.common['Authorization'] = 'Bearer '+`${window.localStorage.getItem('Authoriaztion')}`;

require('@/assets/resume/js/vendor/jquery-2.2.4.min.js')
require('@/assets/resume/js/vendor/bootstrap.min.js')
require('@/assets/resume/js/jquery.ajaxchimp.min.js')
require('@/assets/resume/js/jquery.magnific-popup.min.js')
require('@/assets/resume/js/parallax.min.js')
require('@/assets/resume/js/owl.carousel.min.js')
require('@/assets/resume/js/jquery.sticky.js')
require('@/assets/resume/js/main.js')


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})

