      <div class="row">
      <Sidebar/>

        <main role="main" class="col-md-9 ml-sm-auto col-lg-10 px-4">
        <bikemap/>
        </main>
      </div>
    </div>
    </div>
</template>

<script>
import Navbar from "@/components/ubike/detail/Navbar";
import Sidebar from "@/components/ubike/detail/sidebar";
import Alter from "./AlterMessage";
import bikemap from "@/components/ubike/main";
//import map from "@/components/ubike/detail/map";
import { mapActions } from 'vuex';
import { mapGetters } from 'vuex'; 
export default {
    components:{
       Navbar,
       Sidebar,
       Alter,
       bikemap,
       //map
    },
    data() {
      return {
        bikedata:[],
      }
    },
    methods: {
      ...mapActions({           
           FilterUbikeData:'FilterUbikeData',
           getkkk:'test',
		 }),     
    },//end methods
    mounted() {
      this.FilterUbikeData('台北');
      //this.getkkk();
    },
}
</script>


