<template>
  <div id="app">
    <!-- 初始化地圖設定 -->
    <l-map
      ref="myMap"
      :zoom="zoom"
      :center="center"
      :options="options"
      style="height: 100vh;">
      <!-- 載入圖資 -->
      <l-tile-layer :url="url" :attribution="attribution" />
      
      <!-- 自己所在位置 -->
      <!-- <l-marker ref="location" :lat-lng="center">
        <l-popup>
          你的位置
        </l-popup>
      </l-marker> -->


      <!-- 創建標記點 -->
      <l-marker :lat-lng="item.local" v-for="item in SelectData" :key="item.id">
        <!-- 標記點樣式判斷 -->
        <l-icon
          :icon-url="item.name === '夢時代購物中心'?icon.type.gold:icon.type.black"
          :shadow-url="icon.shadowUrl"
          :icon-size="icon.iconSize"
          :icon-anchor="icon.iconAnchor"
          :popup-anchor="icon.popupAnchor"
          :shadow-size="icon.shadowSize"
        />
        <!-- 彈出視窗 -->
        <l-popup>
         <div>
           <div><h5>{{item.name}}</h5></div>
           <div>可租借車輛剩餘:{{item.remaining}}</div>
           <div>可停空位剩餘:{{item.stock}}</div>
           <div>更新時間:{{item.upddate}}</div>
         </div>
        </l-popup>
      </l-marker>
    </l-map>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import { mapGetters } from 'vuex';
import citydata from '@/assets/city.json';
export default { 
  name: "main_bike",
  data() {
    return {
      city:citydata,
      zoom: 14,
      center:[25.0408578889, 121.567904444],
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      attribution: `© <a href="http://osm.org/copyright">OpenStreetMap</a> contributors`,
      options: {
        zoomControl: false
      },
      icon: {
        type: {
          black:
            "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-black.png",
          gold:
            "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-gold.png"
        },
        shadowUrl:
          "https://cdnjs.cloudflare.com/ajax/libs/leaflet/0.7.7/images/marker-shadow.png",
        iconSize: [25, 41],
        iconAnchor: [12, 41],
        popupAnchor: [1, -34],
        shadowSize: [41, 41]
      }
    };
  },
   methods:{
     ...mapActions({
          //UbikeData:'UbikeData'         
        }), 
      updatemap(){  
        if(this.CityName=='台北') { this.city[0].districts.find((dist) => {  if (dist.name === this.AreaName) {this.center=[dist.latitude, dist.longitude];}  }); }
        if(this.CityName=='高雄') {if(this.AreaName=='鹽埕區'){this.center=[22.62290 ,120.28015];}else{this.center=this.CentrePoint;} }
      },
       
  },
  computed:{
     ...mapGetters(
        {
          CityName:'getCityName',
          AreaName:'getAreaName', 
          CentrePoint:'getCentrePoint',
          SelectData:'gerareanameDataArray',       
        }),     
  },
  watch:{
    AreaName(){     
      this.updatemap();
    },
  },
  mounted() {   
  // 等地圖創建後執行
    // this.$nextTick(() => {
    //   // 獲得目前位置
    //   navigator.geolocation.getCurrentPosition(position => {
    //     const p = position.coords;
    //     // 將中心點設為目前的位置
    //     this.center = [p.latitude, p.longitude];
    //     // 將目前的位置的標記點彈跳視窗打開
    //     this.$refs.location.mapObject.openPopup();       
    //   });
    // });
  
  }
};
</script>

<style>
  html,body {
    padding: 0;
    margin: 0;
  }
</style>


